import React, { useState } from "react";
import SchoolIcon from '@mui/icons-material/School';
import LaptopIcon from '@mui/icons-material/Laptop';
import AccountTreeIcon from '@mui/icons-material/AccountTree';
import InterestsIcon from '@mui/icons-material/Interests';
import './resume.css'


const Resume = () => {
  const [Education, setEducation] = useState(true);
  const [Skills, setSkills] = useState(false);
  const [Project, setProject] = useState(false);
  const [Interests, setInterests] = useState(false);
  // const [Info, setInfo] = useState([ "Education", "Skills", "Project", <h1>M</h1> ]);
  // const [Index, setIndex] = useState(0);
  let _setEducation = () => {
    setEducation(!Education);
    setSkills(false);
    setProject(false);
    setInterests(false);
  };
  
  let _setSkills = () => {
    setSkills(!Skills);
    setEducation(false);
    setProject(false);
    setInterests(false);
  };

  let _setProject = () => {
    setProject(!Project);
    setEducation(false);
    setSkills(false);
    setInterests(false);
  };
  let _setInterests = () => {
    setInterests(!Interests);
    setEducation(false);
    setProject(false);
    setSkills(false);
  };

  return (
    <div id='resume' className="resume-1">
       <h2>Resume</h2>
    <div className="resume">
   
         <div className="resume-button">
        {/* <button onClick={()=>setData(true)}>show</button>
        <button onClick={()=>setData(false)}>hide</button> */}
         <div className="ICONS">
          <ul><SchoolIcon/></ul>
          <ul><LaptopIcon/></ul>
          <ul><AccountTreeIcon/></ul>
          <ul><InterestsIcon/></ul>
        </div> 
        <div className=""></div>
        <button className="btn" onClick={() => _setEducation()}>Education</button>
        <button className="btn" onClick={() => _setSkills()}>Skills</button>
        <button className="btn" onClick={() => _setProject()}>Project</button>
        <button className="btn" onClick={() => _setInterests()}>Interests</button>
      </div>
       <div className="resume-item">                                       
       <div className="eduction"> {Education ? <div>

         <ul><h4>College/University</h4>
         <li>Krishna Engineering College/(AKTU) </li><span>2022</span>
         </ul>
         <ul><h4>Class 12th </h4></ul>
         <ul><li>A.S.V.J Sr. Sec. school</li><span>2017</span></ul>
         <ul><h4>Class 10th </h4></ul>
         <ul><li>Govt. Boys Sr. Sec. school</li><span>2015</span></ul>
       </div> : null}
       </div>
       <div className="skill">{Skills ? <div>
         <ul><h4><li>HTML</li></h4></ul>
         <ul><h4><li>CSS</li></h4></ul>
         <ul><h4><li>JAVASCRIPT</li></h4></ul>
         <ul><h4><li>REACT JS</li></h4></ul>
       </div> : null}</div>
      <div className="projects">  {Project ? <div>
        <li><h4>Presonal Portfolio Website</h4></li>
        <ul>Technologies Used: React JS</ul>
        <p>A presonal portfolio website to showcase all my details and projects at one place.</p>
        <li><h4>Chat web app</h4></li>
        <ul>Technologies Used: React JS,styled-components,Redux</ul>
        <p>A presonal portfolio website to showcase all my details and projects at one place.</p>
        <li><h4>Website Generator</h4></li>
        <ul>Technologies Used: React JS,styled-components,Redux</ul>
        <p>Online ecommerce website,and with management system </p>
        <li><h4>SpaceX</h4></li>
        <ul>Technologies Used: Html,css.javascript</ul>
        <p>SpaceX website with animation</p>
      </div> : null}</div>
      <div className="Interests">  {Interests ? <div>
        <ul><h4><li>Making YouTube Video</li></h4></ul>
        <ul><h4><li>Web Development</li></h4></ul>
        <ul><h4><li>Robotics</li></h4></ul>
      </div> : null}</div>
      
     </div>
     
    </div>
    </div>
  );
};

export default Resume;
