import React from "react";
import "./intro.css";
import "../Header/header.css";
import img from "./images/brij.png";

const intro = () => {
  return (
    <section  id="home" className="hero">
      <div className="hero-i">
        
        <div className="side-intero">
          <img src={img} alt="error" />
        </div>
        <div className="intro-head">
          <div className="hero-head-dece">
            <div className="hero-dec">
              <h2>Hi,</h2>
            </div>
            <div className="hero-name">
              <h2>I'm <span className="brij">Brij,</span></h2>
            </div>
          </div>
          <div className="hero-head">
            <div className="title-wrapper">
              <div className="title-item">
                <h3>Web Developer</h3>
              </div>
              <div className="title-item">
                <h3>React develpoer</h3>
              </div>
            </div>
          </div>
          <div className="hero-head-dec">
            <p>
              I'm a fronted develpoer with basic knowledge in designing great
              User interface and develop real life interactions
              I'm also a youtuber.
            </p>
          </div>
          <div className="btn"> <a href="https://drive.google.com/file/d/1mGXmBxaAw6J8KrS3S60iGzeGEiU5-uVA/view?usp=sharing"><button>Get Resume</button></a> </div>
        </div>
      
      </div>
    </section>
  );
};

export default intro;
