import React, { useState } from "react";
import "./header.css";
import Menu from './images/threeline.png'
import CircleIcon from "@mui/icons-material/Circle";
import Threebtn from '../Header/Button'

const Header = () => {
  const[data,setData]=useState(false)
  return (
    <>
     
      <header className="header-container">
      <button className="mobile-nav-btn" onClick={()=>setData(!data)}><img src={Menu} alt=""/></button>
      <div>{data ?<Threebtn/>:null}</div>

        <nav>
         
            <div className="logo">
              <CircleIcon />
              <span>Coders</span>
            </div>
         
          <div class="right-navbar">
            <div class="nav-links">
              <ul>
                <a href="#home"><li className="active">Home</li></a>
                <a href="#about"><li>About</li></a>
                <a href="#home"><li>Portfolio</li></a>
                <a href="#resume"><li>Resume</li></a>
                <li>Blog</li>
              </ul>
            </div>
            <div className="btn">
             <a href="#contact"> <button>Contact</button></a>
            </div>
          </div>
        </nav>
        
      </header>
      
    </>
  );
};

export default Header;
