 import React from 'react';
import './projects.css';
import Picture from '../Projects/images/spaceX.png';
import IMG from '../Projects/images/SPACEX1.PNG';
import IMGs from '../Projects/images/portifolio.png';
import website from '../Projects/images/website.png';
import custom from '../Projects/images/custom.png'
import gym from '../Projects/images/gympage.png'

const Project = () => {
  return (
    <div className='project'>
        <div className='project-head'>
            <h2><span>My </span>Project</h2>
        </div>
        <div className='project-items'>
            <div className='project-items-a' >
                <img src={ website} alt="" />
                <h4>Website Genrater</h4>
                <p>using React js ,styled-component,Redux</p>
                <a href="https://github.com/brij123k/Final-website.project.git"><h5>Show code</h5></a>
            </div>
            <div className='project-items-a' >
                <img src={ custom} alt="" />
                <h4>Custom page of Website Genrater</h4>
                <a href='https://github.com/brij123k/generater.git'><h5>Show code</h5></a>
            </div>
            <div className='project-items-a' >
                <img src={ IMGs} alt="" />
                <h4>Portfolio</h4>
                <p>Using React js</p>
            </div>
            <div className='project-items-a' >
                <img src={ Picture} alt="" />
                <h4>SpaceX</h4>
                <p></p>
            </div>
            <div className='project-items-a' >
                <img src={ gym} alt="" />
                <h4>Gym</h4>
                <a href='https://github.com/brij123k/gympage'><h5>Show code</h5></a>
                <p></p>
            </div>
            <div className='project-items-a' >
                <a href="https://brij123k.github.io/SpaceX/"><img className='spac' src={ IMG} alt="" />
                <h4>SpaceX</h4></a>
                <p>Using Html,Css,JavaScript</p>
            </div>
            
        </div>
    </div>
  )
}

export default Project